const state = () => ({
    weirdStuff: [
        {
            img: 'channel-logo.png',
            isActive: true
        },
        {
            img: 'channel-logo.png',
            isActive: false
        },
        {
            img: 'channel-logo.png',
            isActive: false
        }
    ]
})

const getters = {
    getAllStuff(state) {
        return state.weirdStuff;
    }
}

const mutations = {
    changeStuff(state, index) {
        state.weirdStuff.forEach((item, i) => {
            item.isActive = index === i;
        });
    }
}

export default {
    namespaced: true,
    state,
    getters,
    mutations
}