<template>
    <li 
        class="friend-item" 
        :class="{active: getActiveChatID == friend.chat_id}" 
        @click="openChat(friend)"
    >
        <status-indecator :isOnline="true" />
        <avatar-small :image="friend.avatar" alt="avatar" />
        <div class="name">{{friend.name}}</div>
    </li>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex'
import StatusIndecator from './StatusIndecator.vue'
import AvatarSmall from '@/components/AvatarSmall.vue'
export default {
    components: { StatusIndecator, AvatarSmall },
    name: 'FriendItem',
    props: ['friend'],
    computed: {
        ...mapGetters({
            getActiveChatID: 'chat/getActiveChatID'
        })
    },
    methods: {
        ...mapMutations({
            changeActiveChat: 'chat/changeActiveChat'
        }),
        openChat(friend) {
            this.changeActiveChat(friend.chat_id);
        }
    }
}
</script>

<style scoped>
    .friend-item {
        display: flex;
        align-items: center;
        padding: 8px 5px 8px 25px;
        cursor: pointer;
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 17px;
        color: #B5B5B5;
        list-style: none;
    }

    .friend-item.active {
        background: #5f6267;
    }

    .friend-item.active .name,
    .friend-item:hover .name {
        color: #fff;
    }

    .friend-item.active .status-indecator:not(.online) {
        background: #888;
    }

    .friend-item.active .status-indecator {
        background: #88fe14;
    }

    .name {
        padding-left: 11px;
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 17px;
        color: #B5B5B5;
    }
</style>