<template>
    <div class="channel-list">
        <div class="channel-title">
            <span>Channels</span>
            <span class="count">{{allChannels.length}}</span>
        </div>
        <ul class="list">
            <li v-for="(channel, index) in channels" 
                class="channel-item"
                :class="{active: channel.chat_id == getActiveChatID}" 
                :key="index"
                @click="showChat(channel)"
            >
                {{channel.name}}
            </li>
        </ul>
    </div>
</template>

<script>
import {mapGetters, mapMutations} from 'vuex'
export default {
    name: 'ChannelList',
    components: {},
    data() {
        return {
            channels: {}
        }
    },
    created() {
        this.channels = this.allChannels.map(item => ({...item, active: false}));
    },
    computed: {
        ...mapGetters({
            allChannels: 'channels/allChannels',
            getActiveChatID: 'chat/getActiveChatID'
        })
    },
    methods: {
        ...mapMutations({
            changeActiveChat: 'chat/changeActiveChat'
        }),
        showChat(item) {
            this.channels.forEach(element => {
                element.active = item.name === element.name;
            });
            this.changeActiveChat(item.chat_id);
        }
    }
}
</script>

<style scoped>
    .channel-list {
        padding: 0;
        list-style: none;
        margin-bottom: 5px;
    }
    .channel-title {
        display: flex;
        justify-content: space-between;
    }
    .channel-title span {
        font-style: normal;
        font-weight: bold;
        font-size: 12px;
        line-height: 14px;
        text-transform: uppercase;
        color: #919191;
    }
    .list {
        padding: 12px 0;
    }
    .channel-item {
        padding: 4px 14px 9px 13px;
        border-radius: 4px;
        cursor: pointer;
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 17px;
        color: #B5B5B5;
        list-style: none;
    }
    .channel-item.active,
    .channel-item:hover {
        color: #fff;
    }
    .channel-item.active {
        color: #fff;
        background: #5f6267;
        font-weight: bold;
        font-size: 14px;
        line-height: 17px;
    }
</style>