const state = () => ({})

const getters = {
    getCurrentTime: () => (date = new Date()) => {
        if (typeof date === 'string') {
            return date;
        }

        let hours = date.getHours().toString();
        let minutes = date.getMinutes().toString();

            if (hours.length === 1) {
                hours = '0' + hours;
            }

            if (minutes.length === 1) {
                minutes = '0' + minutes;
            }

        return `${hours}:${minutes}`;
    },
    getDay: () => (date) => {
        return date.getDay()
    },
    getTimeFromMessage: () => (message) => {
        return message.date.getTime()
    }
}

export default {
    namespaced: true,
    state,
    getters
}