<template>
  <div class="chat-footer">
      <button>
          <img src="../../assets/micro.svg" alt="">
      </button>
      <button>
          <img src="../../assets/share.svg" alt="">
      </button>
      <input type="text" placeholder="Message" v-model="message" @keypress.enter="createNewMessage">
      <emoji-picker :isActive="emojiPickerActive" @add-emoji="addEmoji" />
      <button class="emoji-button" @click="toggleEmojies">
          <img src="../../assets/emojies.svg" alt="">
      </button>
  </div>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex'
import EmojiPicker from './EmojiPicker.vue'
export default {
  components: { EmojiPicker },
   name: 'ChatFooter',
   data() {
       return {
           message: '',
           emojiPickerActive: false
       }
   },
   computed: {
       ...mapGetters({
           getCurrentUserID: 'getCurrentUserID',
           getCurrentTime: 'methods/getCurrentTime',
           getActiveChat: 'chat/getActiveChat',
           isFirstMessage: 'chat/isFirstMessage',

       })

   },
   methods: {
        ...mapMutations({
            sendMessage: 'chat/sendMessage'
            
        }),
        createNewMessage() {
            this.sendMessage({
                message_id: this.getActiveChat.messages[this.getActiveChat.messages.length],
                user_id: this.getCurrentUserID,
                text: this.message,
                date: new Date(),
                firtMessage: this.isFirstMessage
            })

            this.message = '';
        },
        toggleEmojies() {
            this.emojiPickerActive = !this.emojiPickerActive;
        },
        addEmoji(emoji) {
            this.message += emoji;
            this.emojiPickerActive = false;
        }
    }
}
</script>

<style scoped>
    button {
        padding: 0 9px;
    }
    .emoji-button {
        position: relative;
        outline: none;
    }
    .chat-footer {
        position: relative;
        align-items: center;
        display: flex;
        padding: 13px;
        border-top: 1px solid #ccc;
        height: 71px;
    }

    input {
        outline: none;
        margin-bottom: 4px;
        padding: 20px 11px;
        flex-grow: 1;
        font-size: 14px;
        line-height: 17px;
        color: #616161;
    }
</style>