<template>
    <li>
        <button 
            class="stuff"
            :class="{active: isActive}"
            @click="activeItem(index)"
        >
            <img 
                :src="require('@/assets/' + img)" 
                alt=""
            />
        </button>
    </li>
</template>

<script>
import { mapMutations } from 'vuex'
export default {
    name: 'WeirdStuff',
    props: {
        img: String,
        isActive: Boolean,
        index: Number
    },
    methods: {
        ...mapMutations({
            changeStuff: 'weird_stuff/changeStuff'
        }),
        activeItem(index) {
            this.changeStuff(index);
        }
    }
}
</script>

<style scoped>
    li {
        width: 100%;
        padding: 9px 0;
    }

    .stuff {
        position: relative;
        display: block;
        border: none;
        outline: none;
        margin: auto;
        width: 40px;
        height: 40px;
        opacity: 0.6;
        transition: 0.2s;
    }

    .stuff.active {
        box-shadow: 0 0 10px #000;
        opacity: 1;
        border-radius: 50%;
    } 
    .stuff.active::before {
        position: absolute;
        content: '';
        display: block;
        border: 5px solid;
        border-color: #ff000000 #8F8F8F #8F8F8F transparent;
        border-radius: 50%;
        top: 14px;
        left: -20px;
        transform: rotate(316deg);
    }
</style>