const getters = {
    getUsersByID: (state) => (ids) => {
        if (ids instanceof Array) {
            return state.users.filter((item) => {
                return ids.includes(item.id);
            })
        }
        
        if (typeof ids === 'number') {
            return state.users.find(item => {
                return item.id === ids;
            })
        }
    }
}


export default {
    namespaced: true,
    state,
    getters
}