<template>
    <div class="chat">
        <chat-header :entity="getEntity(getActiveChat.type, getActiveChat.own)" />
        <chat-body>
            <message v-for="(message, index) in getActiveChatMessages" 
                :message="message" 
                :index="index" 
                :key="index" 
            />
        </chat-body>
        <chat-footer />
    </div>
</template>

<script>
import { mapGetters } from 'vuex'
import ChatBody from './ChatBody.vue'
import ChatFooter from './ChatFooter.vue'
import ChatHeader from './ChatHeader.vue'
import Message from './Message.vue'

export default {
    name: 'Chat',
    components: { 
        ChatHeader, 
        ChatBody, 
        ChatFooter,
        Message
    },
    computed: {
        ...mapGetters({
            getActiveChat: 'chat/getActiveChat',
            getActiveChatMessages: 'chat/getActiveChatMessages',
            getUsersByID: 'users/getUsersByID',
            getChannelByID: 'channels/getChannelByID',
        }),
        
    },
    methods: {
        getEntity(type, id) {
            if (type === 'user') {
                return this.getUsersByID(id);
            }

            if (type === 'channel') {
                return this.getChannelByID(id);
            }
        }
    }
}
</script>

<style>
    .chat {
        display: flex;
        flex-direction: column;
        flex-grow: 1;
        background: #fff;
    }
</style>