<template>
    <div class="channel-list">
        <ul class="button-group">
            <weird-stuff
                v-for="(stuff, index) in getAllStuff"
                :key="index"
                :img="stuff.img"
                :index="index"
                :isActive="stuff.isActive"
                :stuff="stuff"
             />
        </ul>
    </div>
</template>

<script>
import WeirdStuff from './WeirdStuff'
import { mapGetters } from 'vuex'
export default {
    components: { WeirdStuff },
    name: 'WeirdStuffList',
    computed: {
        ...mapGetters({
            getAllStuff: 'weird_stuff/getAllStuff'
        })
    },

}
</script>

<style scoped>
    .channel-list {
        padding-top: 32px;
    }
    .channel-list .button-group {
        list-style: none;
        padding: 0;
    }
</style>