<template>
    <div class="friend-list">
        <div class="friend-list-title">
            <span>Friends</span>
            <span class="count">{{getFriends.length}}</span>
        </div>
        <ul class="list">
            <friend-item v-for="friend of getUsersByID(getFriends)" :friend="friend" :key="friend.id" />
        </ul>
    </div>
</template>

<script>
import { mapGetters } from 'vuex'
import FriendItem from './FriendItem.vue'
export default {
    name: 'FriendList',
    components: {
        FriendItem
    },
    computed: {
        ...mapGetters({
            getFriends: 'friends/getFriends',
            getUsersByID: 'users/getUsersByID'
        })
    }
}
</script>

<style scoped>
    .list {
        padding: 0;
        list-style: none;
        margin-left: -25px;
        margin-right: -24px;
    }
    .friend-list-title {
        display: flex;
        justify-content: space-between;
        margin-bottom: 10px;
    }
    .friend-list-title span {
        font-style: normal;
        font-weight: bold;
        font-size: 12px;
        line-height: 14px;
        text-transform: uppercase;
        color: #919191;
    }
</style>