// GETTERS
const getters = {
    getActiveChat(state) {
        return state.chatList[state.activeChatID];
    },

    getActiveChatMessages(state) {
        let activeChat = state.chatList[state.activeChatID];

        return activeChat.messages.slice().reverse();
    },

    isFirstMessage(state) {
        let activeChatMessages  = state.chatList[state.activeChatID].messages;

        if (!activeChatMessages.length) {
            return true
        }

        let currentDate         = new Date(),
            lastMessageDate     = activeChatMessages[activeChatMessages.length - 1].date,
            sameDay             = currentDate.getDay() === lastMessageDate.getDay(),
            sameMonth           = currentDate.getMonth() === lastMessageDate.getMonth(),
            sameYear            = currentDate.getYear() === lastMessageDate.getYear();

        if (sameDay && sameMonth && sameYear) {
            return false;
        }

        return true;
    },
    
    getActiveChatID(state) {
        return state.activeChatID;
    },
}

// MUTATIONS
const mutations = {
    changeActiveChat(state, chatID) {
        state.activeChatID = chatID;
    },

    sendMessage(state, message) {
        let activeChat = state.activeChatID.toString();
        state.chatList[activeChat].messages.push(message);
    }
}

export default {
    namespaced: true,
    state,
    getters,
    mutations
}