<template>
    <div>
        <date-divider v-if="message.firtMessage"
            :date="getFormatedDate(message.date)"
        />
        <div class="message d-flex flex-row">
            <avatar-small :image="getUsersByID(message.user_id).avatar" alt="avatar" />
            <div class="text-wrap">
                <div class="title d-flex flex-row align-center">
                    <h3 class="name">{{ getUsersByID(message.user_id).name }}</h3>
                    <p class="time">{{ getCurrentTime(message.date) }}</p>
                </div>

                <div class="text">
                    <p>
                        {{message.text}}
                    </p>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { mapGetters } from 'vuex'
import AvatarSmall from '../AvatarSmall.vue'
import DateDivider from './DateDivider.vue'

export default {
    name: 'Message',
    components: { AvatarSmall, DateDivider },
    props: {
        message: Object,
        index: Number
    },
    computed: {
        ...mapGetters({
            getActiveChat: 'chat/getActiveChat',
            getActiveChatID: 'chat/getActiveChatID',
            getFriends: 'friends/getFriends',
            getUsersByID: 'users/getUsersByID',
            getCurrentTime: 'methods/getCurrentTime',
            getTimeFromMessage: 'methods/getTimeFromMessage',
        }),
    },
    methods: {
        getFormatedDate(date) {
            let months = ['Junuary', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December'];
            let currentDate         = new Date(),
                sameDay             = currentDate.getDay() === date.getDay(),
                sameMonth           = currentDate.getMonth() === date.getMonth(),
                sameYear            = currentDate.getYear() === date.getYear();

            if (sameDay && sameMonth && sameYear) {
                return 'Today';
            } else
            
            if (!sameDay && sameMonth && sameYear) {
                return 'Yesterday';
            }

            if (!sameDay && !sameMonth && sameYear) {
                return `${date.getDate()} ${months[date.getMonth()]}`;
            }

            return `${date.getDate()} ${months[date.getMonth()]} ${date.getFullYear()}`;
        }
    }
}

</script>

<style scoped>
    .title {
        margin-bottom: 2px;
    }
    .name {
        margin: 0;
        font-style: normal;
        font-weight: bold;
        font-size: 12px;
        line-height: 14px;
        color: #000000;
    }

    .time {
        margin: 0;
        padding-left: 7px;
        font-style: normal;
        font-weight: normal;
        font-size: 12px;
        line-height: 14px;
        color: #8D8D8D;
    }

    .text-wrap {
        padding-left: 12px;
    }

    .text {
        font-style: normal;
        font-weight: normal;
        font-size: 13px;
        line-height: 20px;
        color: #000000;
    }
</style>