<template>
    <div class="status-indecator" :class="{online: isOnline}"></div>
</template>

<script>
export default {
    name: 'StatusIndicator',
    props: {
        isOnline: Boolean
    },
    watch: {
        isOnline: function(newValue) {
            this.classes.online = newValue
        }
    }
}
</script>

<style scoped>
    .status-indecator {
        margin-right: 6px;
        background: #666;
        width: 8px;
        height: 8px;
        border-radius: 50%;
    }
    .status-indecator.online {
        background: #70CC16;
    }
</style>